<template>
  <div data-posX="1" data-posY="1" data-x="6" data-y="3" class="widget item">
    <link href="https://fonts.googleapis.com/icon?family=Material+Icons"
          rel="stylesheet">
    <div class="signin-wrapper" v-if="!userCodeOK && !isAuthenticated()">
      <button v-on:click="authorizeHandler()">Sign In</button>
    </div>
    <div class="user-code" v-if="userCodeOK && !isAuthenticated()">
      <h3>Please visit {{ userCode.url }} to connect your mirror :</h3>
      <h2>{{ userCode.code }}</h2>
    </div>
    <div class="menu" v-if="!userCodeOK && !playing && isAuthenticated()">
      <div class="topbar">
        <div class="back-btn menu-btn" v-on:click="backMenu()">
          <i class="material-icons">
            keyboard_backspace
          </i>
          <span class="menu-label topbar-label">Back</span>
        </div>
        <div class="menu-btn logout-btn" v-on:click="logoutHandler">
          <i class="material-icons">
            exit_to_app
          </i>
          <span class="menu-label topbar-label">Logout</span>
        </div>
      </div>

      <div class="search" v-if="displaySearchBar">
        <input type="text" v-model="search" v-on:keyup="searchHandler" placeholder="Search ..." class="searchbar"/>
        <ul>
          <li v-for="(video, index) in searchResult" :key="index" v-on:click="playVideoHandler(video.id.videoId)">
            <img :src="video.snippet.thumbnails.default.url" width="video.snippet.thumbnails.default.width"
                 height="video.snippet.thumbnails.default.height"/>
            <div class="video-text">
              <span class="video-title">{{video.snippet.title}}</span>
              <span class="video-info">
                {{video.snippet.channelTitle}} -
                {{(video.contentDetails || { duration: "PT99M99S" }).duration | duration}} -
                {{video.statistics.viewCount | viewCount}} views
              </span>
            </div>
          </li>
        </ul>
      </div>

      <div class="trends" v-if="displayTrends">
        <ul>
          <li v-for="(video, index) in trendResult" :key="index" v-on:click="playVideoHandler(video.id)">
            <img :src="video.snippet.thumbnails.default.url" width="video.snippet.thumbnails.default.width"
                 height="video.snippet.thumbnails.default.height"/>

            <div class="video-text">
              <span class="video-title">{{video.snippet.title}}</span>
              <span class="video-info">
              {{video.snippet.channelTitle}} -
              {{(video.contentDetails || { duration: "PT99M99S" }).duration | duration}} -
              {{video.statistics.viewCount | viewCount}} views
            </span>
            </div>
          </li>
        </ul>
      </div>

      <div class="main-menu" v-if="!displaySearchBar && !displayTrends">
        <div class="search-btn menu-btn" v-on:click="searchBtn()">
          <i class="material-icons">
            search
          </i>
          <span class="menu-label">Search</span>
        </div>
        <div class="subscriptions-btn menu-btn" v-on:click="trendsHandler()">
          <i class="material-icons">
            whatshot
          </i>
          <span class="menu-label">Trends</span>
        </div>
      </div>

      <div class="horizontal-spacer"></div>
    </div>
    <div class="player-wrapper" v-if="isAuthenticated() && !this.userCodeOK">
      <youtube v-if="displayPlayer" :video-id="videoId" :player-vars="{autoplay: 1}"
               :player-width="640" :player-height="360" @playing="playingHandler()"
               @paused="notPlayingHandler()" @ended="notPlayingHandler()"></youtube>
    </div>
  </div>
</template>

<script>
/* eslint-disable */ // ToDo: delete

/* global CacheManager */

import Vue              from 'vue'
import VueYouTubeEmbed  from 'vue-youtube-embed'
import axios            from 'axios'

Vue.use(VueYouTubeEmbed)

export default {
  name: 'ur-youtube',
  data: () => ({
    playing: false,
    displaySearchBar: false,
    search: '',
    cacheM: null,
    CLIENT_ID_CONNECT: '461392005750-sdvo8o89k5b86fe2drbol5l22ci3eqkc.apps.googleusercontent.com',
    CLIENT_SECRET_CONNECT: 'hucuVXkF9OxxJ76o3jM51kvd',
    videoId: '',
    userCodeOK: false,
    userCode: null,
    deviceCode: '',
    gToken: null,
    searchResult: [],
    trendResult: [],
    displayTrends: false,
    displayPlayer: false
  }),
  filters: {
    viewCount: function (num) {
      const si = [
        {value: 1, symbol: ""},
        {value: 1E3, symbol: "K"},
        {value: 1E6, symbol: "M"},
        {value: 1E9, symbol: "Md"}
      ];
      const rx = /\.0+$|(\.[0-9]*[1-9])0+$/;
      let i
      for (i = si.length - 1; i > 0; i--) {
        if (num >= si[i].value) {
          break;
        }
      }
      return (num / si[i].value).toFixed(1).replace(rx, "$1") + si[i].symbol;
    },
    duration: function (val) {
      return val.slice(2, val.length).replace('M', ':').replace('S', '')
    }
  },
  mounted: function () {
    this.cacheM = new CacheManager('ur-youtube')
    this.gToken = JSON.parse(this.cacheM.get('gToken')) || null;
    this.searchResult = [];
    this.videoId = this.cacheM.get('videoId') || '';
    if (this.videoId.length > 0) {
      this.displayPlayer = true
      this.playVideoHandler(this.videoId)
    }
    if (!this.isAuthenticated()) {
      this.userCodeOK = false
    }
  },
  methods: {
    authorizeHandler: function () {
      axios.post(`https://accounts.google.com/o/oauth2/device/code?client_id=${this.CLIENT_ID_CONNECT}&scope=https://www.googleapis.com/auth/youtube.readonly`).then(response => {
        this.userCodeOK = true
        this.userCode = {
          code: response.data['user_code'],
          url: response.data['verification_url']
        }
        this.deviceCode = response.data['device_code']
        this.poll(() => {
          return axios.post(`https://www.googleapis.com/oauth2/v4/token?client_id=${this.CLIENT_ID_CONNECT}&client_secret=${this.CLIENT_SECRET_CONNECT}&code=${this.deviceCode}&grant_type=http://oauth.net/grant_type/device/1.0`)
        }, response.data['expires_in'] * 1000, response.data['interval'] * 1000).then(response => {
          this.gToken = {
            accessToken: response.data['access_token'],
            expiresIn: response.data['expires_in'],
            refreshToken: response.data['refresh_token'],
            tokenType: response.data['token_type']
          }
          this.cacheM.store('gToken', JSON.stringify(this.gToken));
          this.userCodeOK = false
        }).catch(err => {
          if (err['timedOut']) {
            this.authorizeHandler()
          }
        })
      })
    },
    logoutHandler: function () {
      this.gToken = {
        accessToken: '',
        expiresIn: -1,
        refreshToken: '',
        tokenType: ''
      }
      this.cacheM.clear('gToken');
      this.userCodeOK = false
      this.userCode = {
        code: '',
        url: ''
      }
    },
    searchHandler: function () {
      axios.get(`https://www.googleapis.com/youtube/v3/search?part=snippet&q=${this.search}&type=video`, {
        headers: {
          'Authorization': `Bearer ${this.gToken.accessToken}`
        }
      }).then(response => {
        let tmp = response.data['items']
        let ids = ''
        for (let i = 0; i < tmp.length; i++) {
          ids += `${tmp[i].id.videoId}, `
        }
        axios.get(`https://www.googleapis.com/youtube/v3/videos?part=contentDetails,statistics&id=${ids}`, {
          headers: {
            'Authorization': `Bearer ${this.gToken.accessToken}`
          }
        }).then(response => {
          for (let i = 0; i < response.data['items'].length; i++) {
            tmp[i]['contentDetails'] = response.data['items'][i]['contentDetails']
            tmp[i]['statistics'] = response.data['items'][i]['statistics']
          }
          this.searchResult = tmp
        })
      }).catch(e => {
        if (e) {
          this.refreshToken()
        }
      })
    },
    trendsHandler: function () {
      axios.get(`https://www.googleapis.com/youtube/v3/videos?part=contentDetails,snippet,statistics&chart=mostPopular&maxResults=5&regionCode=FR`, {
        headers: {
          'Authorization': `Bearer ${this.gToken.accessToken}`
        }
      }).then(response => {
        this.trendResult = response.data['items']
        this.displayTrends = true
      }).catch(e => {
        if (e) {
          this.refreshToken()
          this.trendsHandler()
        }
      })
    },
    refreshToken: function () {
      axios.post(`https://www.googleapis.com/oauth2/v4/token?refresh_token=${this.gToken.refreshToken}&client_id=${this.CLIENT_ID_CONNECT}&client_secret=${this.CLIENT_SECRET_CONNECT}&grant_type=refresh_token`).then(response => {
        this.gToken = {
          accessToken: response.data['access_token'],
          expiresIn: response.data['expires_in'],
          refreshToken: this.gToken.refreshToken,
          tokenType: response.data['token_type']
        }
        this.cacheM.store('gToken', JSON.stringify(this.gToken));
      })
    },
    playVideoHandler: function (video) {
      this.videoId = video
      this.cacheM.store('videoId', this.videoId);
      this.playing = true
      this.displayPlayer = true
    },
    playingHandler: function () {
      this.playing = true
    },
    notPlayingHandler: function () {
      this.playing = false
    },
    searchBtn: function () {
      this.displaySearchBar = true
    },
    backMenu: function () {
      if (this.displaySearchBar) {
        this.displaySearchBar = false
      } else if (this.displayTrends) {
        this.displayTrends = false
      } else if (!this.playing && this.videoId.length > 0) {
        this.playing = true
      }
    },
    poll: function (fn, timeout, interval) {
      const endTime = Number(new Date()) + timeout
      const query = ((resolve, reject) => {
        fn().then(response => {
          resolve(response)
        }).catch(() => {
          if (Number(new Date()) < endTime) {
            setTimeout(query, interval, resolve, reject)
          } else {
            reject({timedOut: true})
          }
        })
      })
      return new Promise(query)
    },
    isAuthenticated: function () {
      if (this.gToken === null)
        return false
      else if (this.gToken.accessToken.length > 0)
        return true
      return false
    }
  }
}
</script>

